import Vue from 'vue';
import Router from 'vue-router';
import Full from '@/components/Full';
//按需加载
const homePage = () => import('@/view/homePage');
const jsObject = () => import('@/view/js/jsObject');
const vueStore = () => import('@/view/vue/vueStore');
const vueInfo = () => import('@/view/vue/vueInfo');
const login = () => import('@/view/login');
Vue.use(Router);
export const constantRouterMap = [
	{
		name: 'login',
		path: '/login',
		component: login
	},
	{
		name: 'homePage',
		path: '/',
		component: homePage
	},
	{
		name: 'Full',
		path: '/Full',
		component: Full,
		children: [
			{
				name: 'jsObject',
				path: 'jsObject',
				component: jsObject
			},
			{
				name: 'vueStore',
				path: 'vueStore',
				component: vueStore
			},
			{
				name: 'vueInfo',
				path: 'vueInfo',
				component: vueInfo
			}
		]
	}
];
export const asyncRouterMap = [
	{
		path: '/',
		component: Full,
		children: []
	}
];
export default new Router({
	routes: constantRouterMap
});



