<template>
  <div class="top_header">
    <el-menu default-active="0" class="el-menu-demo" backgroundColor="#409eff" mode="horizontal">
      <el-menu-item
        v-for="(item, index) in headItem"
        :index="String(index)"
        @click="itemSelect(index)"
        :key="index"
      >{{item.name}}</el-menu-item>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: "TopHeader",
  data() {
    return {
      headItem: [
        {
          name: "home",
          path: "/",
          icon: "",
          child: []
        },
        // vue
        {
          name: "Vue",
          path: "/Full/vueInfo",
          icon: "",
          child: [
            {
              name: "info",
              path: "/Full/vueInfo",
              icon: "",
              child: []
            },
            {
              name: "store",
              path: "/Full/vueStore",
              icon: "",
              child: []
            }
          ]
        },
        // js
        {
          name: "js",
          path: "/Full/jsObject",
          icon: "",
          child: [
            {
              name: "jsObject",
              path: "/Full/jsObject",
              icon: "",
              child: []
            }
            // {
            //   name: "js",
            //   path: "/Full/jsObject",
            //   icon: "",
            //   child: []
            // }
          ]
        }
      ]
    };
  },
  created() {
    let leftSideData = JSON.parse(
      window.sessionStorage.getItem("leftSideData")
    );
    if (leftSideData && leftSideData.length > 0) {
      this.$store.dispatch("setLeftSideData", leftSideData);
    }
  },
  methods: {
    itemSelect: function(index) {
      this.$store.dispatch("setLeftSideData", this.headItem[index].child);
      window.sessionStorage.setItem(
        "leftSideData",
        JSON.stringify(this.headItem[index].child)
      );
      if (
        this.headItem[index].path &&
        this.headItem[index].path !== this.$router.currentRoute.path
      ) {
        this.$router.push({
          path: this.headItem[index].path,
          params: { id: "" }
        });
      }
    }
  }
};
</script>
<style lang="scss">
.top_header {
  width: 100%;
  height: 60px;
  overflow-y: hidden;
}
</style>



