import Vue from 'vue';
import App from './App';
import router from './router';
import 'element-ui/lib/theme-chalk/index.css';
import Element from 'element-ui';
import store from './store';
import iSymbol from './components/iSymbol';
Vue.use(Element, { size: 'small', zIndex: 3000 });
Vue.config.productionTip = false;
Vue.component('iSymbol', iSymbol); //图标

router.beforeEach((to, from, next) => {
	if (to.path == '/login') {
		next();
	} else if (window.localStorage.getItem('token')) {
		if (store.getters.pageId.length === 0) {
			store.dispatch('setPageId', [ '101', '102', '103' ]);
			const pageId = store.getters.pageId;
			store.dispatch('GenerateRoutes', { pageId }).then(() => {
				router.addRoutes(store.getters.routers); // 动态添加可访问路由表
				next({ ...to, replace: true }); // hack方法 确保addRoutes已完成
			});
		} else {
			next();
		}
	} else {
		alert('您还没有登录，请先登录');
		next('/login');
	}
});

new Vue({
	el: '#app',
	store,
	router,
	components: { App },
	template: '<App/>'
});



