const actions = {
    // 所有在actions中定义的方法，他们的第一个参数是约定好的：要么是store的对象context
    setToken(context, token) {
        context.commit('setToken', token)
    },
    setUser(context, name) {
        context.commit('setUser', name)
    },
    setPageId(context, pageId) {
        context.commit('setPageId', pageId)
    },
    setLeftSideData(context, data) {
        context.commit('setLeftSideData', data)
    },
    setHandleInfo(context, handleInfo) {
        context.commit('setHandleInfo', handleInfo)
    }
}
export default actions



