import {asyncRouterMap, constantRouterMap} from '../../router'

// 判断用户权限页面 页面路由id
function hasPermission(pageId, route) {
    if (route.meta && route.meta.pageId) {
        return pageId.some((id) => route.meta.pageId.indexOf(id) >= 0)
    } else {
        return true
    }
}

const state = {
    routers: constantRouterMap,
    addRouters: []
}
const getters = {
    routers: (state) => {
        return state.routers
    },
    addRouters: (state) => {
        return state.addRouters
    }
}
const mutations = {
    SET_ROUTERS: (state, routers) => {
        state.addRouters = routers
        // constantRouterMap[1]=routers;
        // state.routers=constantRouterMap;
        state.routers = constantRouterMap.concat(routers)
    }
}
const actions = {
    GenerateRoutes({commit}, data) {
        return new Promise(resolve => {
            const {pageId} = data
            const accessedRouters = asyncRouterMap.filter(v => {
                if (pageId.indexOf('admin') >= 0) return true
                if (hasPermission(pageId, v)) {
                    if (v.children && v.children.length > 0) {
                        v.children = v.children.filter(child => {
                            if (hasPermission(pageId, child)) {
                                return child
                            }
                            return false
                        })
                        return v
                    } else {
                        return v
                    }
                }
                return false
            })
            commit('SET_ROUTERS', accessedRouters)
            resolve()
        })
    }
}

export default {state, getters, actions, mutations}



